import React from "react";
import { Button } from "native-base";

function UI() {
  const clickHandler = (e) => {
    console.log("User Relationship Create Button");
  };

  return (
    <Button
      _text={{ color: "white", fontSize: ["13", "14"], fontWeight: "600" }}
      p="8px"
      rounded="8px"
      w="full"
      bgColor="green.500"
      onPress={clickHandler}
    >
      Thm bn b
    </Button>
  );
}
export default UI;

